"use client";

import { useRef, useState, useCallback } from "react";

interface UseSpeechRecognitionProps {
  language?: string;
  onResult: (text: string) => void;
}

export default function useSpeechRecognition({ 
  language = "hi-IN", 
  onResult 
}: UseSpeechRecognitionProps) {
  const recognitionRef = useRef<any>(null);
  const [isListening, setIsListening] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // Check for browser support
  const SpeechRecognition = 
    typeof window !== "undefined" && 
    ((window as any).SpeechRecognition || (window as any).webkitSpeechRecognition);

  const startListening = useCallback(() => {
    if (!SpeechRecognition) {
      setError("рдЖрдкрдХрд╛ рдмреНрд░рд╛рдЙрдЬрд╝рд░ рд╡реЙрдЗрд╕ рд░рд┐рдХреЙрдЧреНрдирд┐рд╢рди рдХреЛ рд╕рдкреЛрд░реНрдЯ рдирд╣реАрдВ рдХрд░рддрд╛");
      return;
    }

    try {
      recognitionRef.current = new SpeechRecognition();
      recognitionRef.current.lang = language;
      recognitionRef.current.interimResults = false;
      recognitionRef.current.maxAlternatives = 1;
      recognitionRef.current.continuous = false;

      recognitionRef.current.onstart = () => {
        setIsListening(true);
        setError(null);
      };

      recognitionRef.current.onresult = (event: any) => {
        const transcript = event.results[0][0].transcript;
        onResult(transcript);
        setIsListening(false);
      };

      recognitionRef.current.onerror = (event: any) => {
        console.error("Speech recognition error:", event.error);
        setError("рд╡реЙрдЗрд╕ рд░рд┐рдХреЙрдЧреНрдирд┐рд╢рди рдореЗрдВ рддреНрд░реБрдЯрд┐ рд╣реБрдИ");
        setIsListening(false);
      };

      recognitionRef.current.onend = () => {
        setIsListening(false);
      };

      recognitionRef.current.start();
    } catch (error) {
      console.error("Error starting speech recognition:", error);
      setError("рд╡реЙрдЗрд╕ рд░рд┐рдХреЙрдЧреНрдирд┐рд╢рди рд╢реБрд░реВ рдирд╣реАрдВ рд╣реЛ рд╕рдХрд╛");
      setIsListening(false);
    }
  }, [SpeechRecognition, language, onResult]);

  const stopListening = useCallback(() => {
    if (recognitionRef.current) {
      recognitionRef.current.stop();
    }
    setIsListening(false);
  }, []);

  return { 
    startListening, 
    stopListening, 
    isListening, 
    error,
    isSupported: !!SpeechRecognition
  };
}
