"use client";

import React, { useState, useEffect } from "react";
import useSpeechRecognition from "@/hooks/useSpeechRecognition";

interface VoiceCommandProps {
  onResult: (result: string) => void;
  language?: string;
  placeholder?: string;
}

const VoiceCommand: React.FC<VoiceCommandProps> = ({ 
  onResult, 
  language = "hi-IN",
  placeholder = "рдмреЛрд▓реЗрдВ..."
}) => {
  const [mounted, setMounted] = useState(false);
  const { startListening, stopListening, isListening, error, isSupported } = useSpeechRecognition({
    language,
    onResult,
  });

  useEffect(() => {
    setMounted(true);
  }, []);

  const toggleListening = () => {
    if (isListening) {
      stopListening();
    } else {
      startListening();
    }
  };

  // Don't render until mounted to avoid hydration mismatch
  if (!mounted) {
    return (
      <div className="mt-2">
        <button
          type="button"
          disabled
          className="px-4 py-2 rounded-lg font-medium bg-gray-300 text-gray-500"
        >
          ЁЯОд рд▓реЛрдб рд╣реЛ рд░рд╣рд╛ рд╣реИ...
        </button>
      </div>
    );
  }

  if (!isSupported) {
    return (
      <div className="mt-2 p-2 bg-red-100 text-red-700 rounded text-sm">
        рдЖрдкрдХрд╛ рдмреНрд░рд╛рдЙрдЬрд╝рд░ рд╡реЙрдЗрд╕ рдХрдорд╛рдВрдб рдХреЛ рд╕рдкреЛрд░реНрдЯ рдирд╣реАрдВ рдХрд░рддрд╛
      </div>
    );
  }

  return (
    <div className="mt-2">
      <button
        type="button"
        onClick={toggleListening}
        className={`px-4 py-2 rounded-lg font-medium transition-all duration-200 ${
          isListening
            ? "bg-red-500 text-white animate-pulse"
            : "bg-blue-500 text-white hover:bg-blue-600"
        }`}
        disabled={!isSupported}
      >
        {isListening ? (
          <>
            ЁЯОд рд╕реБрди рд░рд╣рд╛ рд╣реИ... (рд░реБрдХрдиреЗ рдХреЗ рд▓рд┐рдП рдХреНрд▓рд┐рдХ рдХрд░реЗрдВ)
          </>
        ) : (
          <>
            ЁЯОд рдмреЛрд▓реЗрдВ
          </>
        )}
      </button>
      
      {isListening && (
        <div className="mt-2 p-2 bg-blue-100 text-blue-700 rounded text-sm animate-pulse">
          ЁЯО╡ рдХреГрдкрдпрд╛ рдЕрдм рдмреЛрд▓реЗрдВ...
        </div>
      )}
      
      {error && (
        <div className="mt-2 p-2 bg-red-100 text-red-700 rounded text-sm">
          тЪая╕П {error}
        </div>
      )}
    </div>
  );
};

export default VoiceCommand;
